import { resolvePromise } from "./resolvePromise";
import { getDataFromApi } from "./api/restaurantSource";

export const model = {
    user: null,
    ready: false,
    allRestaurants: [],
    topFilters: [],
    sideFilters: [],
    availableFilters: {foodCategories: ["Hamburger", "Pizza", "Taco's", "Coffee"], deliveryTimes: ["0-10 min", "10-30 min", "30-60 min", "1 hour+"], priceRanges: ["$", "$$", "$$$", "$$$$"]},
    availableSideFilters: ["Hamburgers", "Pizza", "Taco", "Coffee", "Fries", "Breakfast"],
    filterResultsPromiseState: { promise: null, data: null, error: null },

    setUser(user){
        this.user = user
    },

    setReady(value){
        this.ready = value
    },

    setRestaurants(restaurants){
        this.allRestaurants = restaurants;
    },

    setTopFilter(topfilter){
        this.topFilters = topfilter;
    },

    setSideFilter(sidefilter){
        this.sideFilters = sidefilter;
    },

    getRestaurants(){
        return this.allRestaurants
    },

    toggleTopFilter(filter) {
        if (this.topFilters.includes(filter)) {
            this.topFilters = this.topFilters.filter(f => f !== filter);
        } else {
            this.topFilters.push(filter);
        }
    },

    toggleSideFilter(filter) {
        if (this.sideFilters.includes(filter)) {
            this.sideFilters = this.sideFilters.filter(f => f !== filter);
        } else {
            this.sideFilters.push(filter);
        }
    },

    getRestaurantsShown() {
        let filtered = this.filterResultsPromiseState.data || [];

        if (this.topFilters.length > 0) {
            filtered = filtered.filter(r =>
                this.topFilters.some(f =>
                    r.filter_ids?.includes(f)
                )
            );
        }

        if (this.sideFilters.length > 0) {
            filtered = filtered.filter(r =>
                this.sideFilters.some(f =>
                    r.filter_ids?.includes(f) ||
                    r.price_range_id === f
                )
            );
        }

        return filtered;
    }

};

